/* *************************************** */
/* ===== GLOBAL CSS ===== */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-box-sizing: border-box;
  /* background-color: whitesmoke; */
}
html,
body {
  height: 100vh;
  width: 100%;
  font-size: 20px;
  font-family: 'Ubuntu', 'Roboto' sans-serif;
  display: inline-block;
  text-rendering: optimizeSpeed;
}

/* ===== texto ===== */
h1 {
  color: #fca728;
  font-weight: bolder;
  text-align: center;
}

/* ===== navbar ===== */
.nav-link:hover {
  color: #fca728 !important;
}

/* ===== button ===== */
.btn {
  border-radius: 20px !important;
  border: none !important;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16) !important;
  background-color: #fca728 !important;
  color: #000000 !important;
  margin: 5px !important;
  padding: 3px 10px 3px 10px !important;
}
.btn:hover {
  background-color: #c9c5c0 !important;
}
.btn-idioma:hover {
  background-color: #fca728ab !important;
}

/* *************************************** */
/* ===== SPINNER ===== */
.spinner-title h2 {
  text-transform: uppercase;
  font-size: 18px;
  text-align: center;
  margin-top: 60px;
}
.spinner-wrapper {
  color: #fca728;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 40%;
}

/* *************************************** */
/* ===== MODAL CONTENT ===== */
/* ===== modal login/signup ===== */
.modal-logo img {
  width: 200px;
  height: auto;
}
.form-group {
  margin: 15px;
}
.modal-body input {
  width: 250px;
  height: 38px;
  font-size: 18px;
  background: #d1c5c540 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 50px;
  opacity: 1;
  border: 0;
  padding: 10px;
  margin: 5px;
  outline: none;
}
.modal-body select {
  width: 250px;
  height: 38px;
  font-size: 18px;
  background: #d1c5c540 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 50px;
  opacity: 1;
  border: 0;
  padding: 10px;
  margin: 5px;
  outline: none;
}
.modal-body h4 {
  color: #fca728;
  text-align: center;
}
.enlace-form {
  text-align: center;
}
.boton-form {
  text-align: center;
  margin-top: 10px;
}
.me-olvide {
  color: #fca728;
  text-decoration: underline;
}
.pointer {
  cursor: pointer;
}

/* ===== modal noparte ===== */
.modal-title {
  margin-bottom: 20px;
}
.que-hacer-button {
  height: 120px;
  width: 150px;
  border-radius: 20px;
  font-size: 15px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.botones {
  text-align: center;
}
.icon-video {
  margin-top: 10px;
}

/* *************************************** */
/* ===== HOME ===== */
/* ===== header ===== */
header {
  position: relative;
}
.header-contenido {
  text-align: center;
}

/* ===== form ===== */
header input {
  background: #a79f9f40 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 50px;
  opacity: 1;
  border: 0;
  outline: none;
}

/* ===== link modal ===== */
.button-link {
  color: #fca728;
  border: none;
  background-color: transparent;
  outline: none;
}
.button-link:focus {
  border: none;
  background-color: transparent;
  outline: none;
}
.modal-title {
  color: #fca728;
}
.tutorial-text:hover {
  text-decoration: #000000 underline;
}

/* *************************************** */
/* ===== CONTACTO ===== */
.contacto-contenido .row {
  text-align: center;
}
.contacto-texto {
  padding-bottom: 40px;
}
.contacto-grupo {
  margin-bottom: 40px !important;
}
.contacto-grupo img {
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16) !important;
}
.contacto-contenido h4 {
  padding-top: 10px;
  font-size: 25px;
}
.contacto-email {
  text-decoration: underline;
  font-size: 18px;
}
.contacto-telefono {
  color: black;
  font-size: 18px;
}

/* *************************************** */
/* ===== COTIZACION ===== */
.cotizacion-title h1 {
  padding-top: 20px;
}
.cotizacion-title p {
  color: #fca728;
  font-size: 20px;
  text-align: left;
  margin-top: 20px;
}
.nota {
  font-size: 10px;
  padding: 15px 5px 15px 25px;
}
.nota span {
  font-size: 15px;
}
.beneficios {
  border-radius: 15px;
  /* width: 600px; */
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
}
.titulo-jumbo {
  font-size: 25px;
  color: #fca728;
  text-align: center;
  margin-top: 30px;
}
.beneficios-jumbo {
  color: whitesmoke;
  text-align: left;
  font-size: 12px;
  margin: 5px !important;
}
.beneficios-jumbo:last-child {
  margin-bottom: 30px !important;
}
.btn-comprar {
  color: whitesmoke !important;
  font-size: 25px !important;
  width: 159px;
}

/* *************************************** */
/* ===== HACEMOS ===== */
.hacemos-texto {
  text-align: justify;
}

/* *************************************** */
/* ===== SOMOS ===== */
.somos-contenido h2 {
  text-align: center;
  margin-bottom: 40px;
}
.somos-texto {
  text-align: justify;
  margin-bottom: 40px;
}

/* *************************************** */
/* ===== TUTORIAL ===== */
.tutorial .row {
  margin-bottom: 20px !important;
}
.tutorial-subtitulo {
  margin-bottom: 50px;
}
.tutorial p {
  font-size: 20px;
}
.tutorial-contenido span {
  color: #fca728;
  font-size: 15px;
}
.tutorial-enlace {
  color: #fca728;
}
.tutorial-enlace:hover {
  text-decoration: #fca728 underline;
}
.logo-video {
  float: right;
  padding-right: 40px;
}
.texto-logo-tutorial {
  float: left;
  padding-left: 40px;
}
.tutorial-video {
  display: flex;
  float: inline-start;
}
.tutorial-video-texto span {
  font-size: 120px;
  color: #fca728;
}

/* *************************************** */
/* ===== BUSCAMOS ===== */
.description {
  width: 250px !important;
  background: #d1c5c540 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 10px !important;
  opacity: 1;
  border: 0;
}

/* ===== IDIOMA ===== */
.pais-idioma {
  margin-top: 15px;
}
.btn-idioma {
  font-size: 15px !important;
  height: 30px !important;
  border-radius: 15px !important;
  margin-top: 0px !important;
}

/* *************************************** */
/* ===== MODAL ===== */
.site-modal {
  position: fixed; /* Stay in place */
  z-index: 10000; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 90%;
  border-radius: 20px;
}

.site-modal .close {
  color: #fca728;
  float: right;
  font-size: 28px;
  font-weight: bold;
  width: 23px;
  padding: 4px;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

/* ===== PAIS ===== */
.pais-idioma {
  margin-top: 15px;
}
.pais-bandera {
  height: 25px;
  width: auto;
  margin-right: 5px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16) !important;
}
.pais-texto {
  font-size: 20px;
}
